<template>
    <div v-if="Object.keys(user).length>0 && Object.keys(Article).length>0">
       <div class="w-100 input d-flex bg-white border-b align-items-center px-3">
            <input type="text" maxlength="80" class=" flex-1 titleInput px-2" 
            v-model="Article.title" placeholder="输入文章标题..." @input="editorChange">
            <div class="text-grey-light d-flex align-items-center">
                <span class="d-none d-md-flex flex-column" v-if="Article.type != 'public'">
                    <span >{{message}}</span>
                    <span v-if="message == '已自动保存至'" class="fs-xxs text-right">{{Article.updatedAt | date}}</span>
                </span>
                <button class="btn text-grey-light py-1 px-2 fs-sm ml-3" 
                style="border-color:#f0f2f7" @click="$router.push(`/editor/drafts`)">草稿</button>
            </div>
            <div class="position-relative mx-3">
               <button class="text-red bg-white border-0" style="outline:none"
               @click.stop="publishClick()">
                    <span v-if="Article.type == 'public'">保存</span>
                    <span v-else>发布</span>
                    <svg class="icon" style="padding:3px" :class="{'Reversal':publish}" aria-hidden="true">
                        <use xlink:href="#icon-sanjiaoxing"></use>
                    </svg>
               </button>
                <category-choice v-show="publish" @Public="Public()"
                :user="user" :categoriesDefalut="Article.categories" :isDraft="Article.type == 'private'"
                @CateChange="CateChange" ref="catePanel"></category-choice>
            </div>
             <avatar :user="user"></avatar>
       </div>
        <mavon-editor
            class="editor-body" style="z-index:1"
            v-model="Article.content" @change="editorChange" @save="autoSave"
            @imgAdd="imgAdd" ref="mavon"
            :toolbars = "toolbars" placeholder="开始编辑文章..."
        ></mavon-editor>
    </div>
</template>

<script>
    import CategoryChoice from './childCmps/CategoryChoice.vue'
    import Avatar from '../Main/childCmps/TopBar/Avatar.vue'
    
    export default {
        props:{
            id:String
        },
        data () {
            return {
                Article:{
                    content:"",
                    title:"",
                    categories:[],
                    type:'private'
                },
                publish:false,
                message:"文章将会自动保存至",
                lazy:null,
                onceFlag:true,
                toolbars: {
                    bold: true, // 粗体
                    italic: true, // 斜体
                    header: true, // 标题
                    underline: true, // 下划线
                    strikethrough: true, // 中划线
                    mark: true, // 标记
                    superscript: true, // 上角标
                    subscript: true, // 下角标
                    quote: true, // 引用
                    ol: true, // 有序列表
                    ul: true, // 无序列表
                    link: true, // 链接
                    imagelink: true, // 图片链接
                    code: true, // code
                    table: true, // 表格
                    fullscreen: false, // 全屏编辑
                    readmodel: true, // 沉浸式阅读
                    htmlcode: true, // 展示html源码
                    help: true, // 帮助
                    /* 1.3.5 */
                    undo: true, // 上一步
                    redo: true, // 下一步
                    trash: true, // 清空
                    save: true, // 保存（触发events中的save事件）
                    /* 1.4.2 */
                    navigation: true, // 导航目录
                    /* 2.1.8 */
                    alignleft: true, // 左对齐
                    aligncenter: true, // 居中
                    alignright: true, // 右对齐
                    /* 2.2.1 */
                    subfield: true, // 单双栏模式
                    preview: true, // 预览
                },
                user:{},
                published:false
               
            }
        },
        methods:{
            publishClick(){ //用于监听发布按钮是否被单击（转换三角形css）
                this.publish = !this.publish
            },
            async autoSave(){  //根据文章标题和内容是否存在值，进行更新或新建文章
                if(this.Article.title != '' && this.Article.content != ''){
                    this.message = "文章正在自动保存..."
                    let res = null
                    if(!this.id) res = await this.$http.post('articles/new',this.Article) 
                    else {
                        res = await this.$http.put(`articles/${this.id}`,this.Article)
                        this.fetchArticle()
                    }
                    if(res.data ){
                        this.message = '已自动保存至'
                        if(res.data[0])  this.$router.replace(`/editor/${res.data[0]['_id']}`)
                        else if(res.data.ok != 1){
                            this.message = "保存出错"
                            this.$router.replace('/editor/new')
                        }
                    }
                } 
            },
            editorChange(){ //监听编辑器的标题和正文部分是否发生改变，节流调用autoSave
                if(this.onceFlag){
                     this.onceFlag = false
                     return
                }else if(this.Article.type != 'public'){
                    if(this.lazy) clearTimeout(this.lazy)
                    this.lazy = setTimeout(this.autoSave,2000)
                }
                
                // this.debounce(this.autoSave,1000)
            },
            async fetchArticle(){
                if(this.id){
                    const res = await this.$http.get(`articles/${this.id}`)
                    this.Article = res.data
                }
            },
            async imgAdd(pos, $file){ //图片上传回调Mavon
                var formdata = new FormData()
                formdata.append('file', $file)
                const res = await this.$http.post('upload',formdata)
                this.$refs.mavon.$img2Url(pos,res.data.url)
            },
            CateChange(categories){ //监测分类是否发生了变化
                this.Article.categories = categories
                this.editorChange()
            },
            Public(){ //发布文章
                if(this.Article.categories.length == 0){
                    this.$message.warning("请至少选择一个分类")
                    return
                }
                if(this.Article.title.length == 0){
                    this.$message.warning("请填写标题")
                    return
                }
                if(this.Article.content.length == 0){
                    this.$message.warning("请填写内容")
                    return
                }
                this.Article.type = 'public'
                this.autoSave()
                this.published = true
                this.$router.push('/')
                this.$message.success("发布成功！")
            },
            async fetchUser(){
                const res = await this.$http.get('users/info')
                this.user = res.data
            }

        },
        components:{
            CategoryChoice,
            Avatar
        },
        created(){
            if(!this.$store.state.Logged) this.$router.go(-1)
            this.fetchArticle()
            this.fetchUser()
            document.addEventListener('click',(e)=>{
                if(this.$refs.catePanel){
                    let isSelf = this.$refs.catePanel.$el.contains(e.target)
                    if(!isSelf){
                        this.publish = false
                    }
                }
            })
        },
        // beforeRouteLeave(to, from, next){
        //     // 判断是否保存当前文章，并确定是否为发布链接跳转
        //     if(!this.published && this.message != '已自动保存至'){
        //         this.$MessageBox.confirm('当前编辑器存在未保存内容，是否退出编辑？', '提示', {
        //             confirmButtonText: '确定',
        //             cancelButtonText: '取消',
        //         }).then(() => {
        //             next()
        //         })
        //     }else{
        //         next()
        //     }
            
        // }
    }
</script>

<style lang="scss" scoped>
    .input{
        height: 60px;
    }
    .editor-body{
        position: absolute;
        top: 60px;
        bottom: 0;
        left: 0;
        right: 0;
    }
    .titleInput{
        font-size: 1.5rem;
        width: 0;
        font-weight: bold;
        border: none;
    }
    .Reversal{
        transition: 100ms;
        transform:rotate(180deg);
    }

</style>